.cards {
  display: grid;
  justify-content: center;
  grid-template-columns: repeat(4,1fr);
  width: 1024px;
  margin: 0 auto;
  padding: 50px 40px;
}

.cards__card {
  margin-right: 48px;
  margin-bottom: 46px;

  &:nth-child(4n) {
    margin-right: 0;
  }
  &:nth-last-child(-n + 4) {
    margin-bottom: 0;
  }
}

.card {
  width: 200px;
  border: 1px solid $text-background-color;
  border-radius: 5px;
  padding: 32px 16px 16px;
  height: 408px;
  background: $white-color;
  transition: transform 0.3s ease;

  &:hover {
    transform: scale(1.2);
  }

  &__image {
    display: block;
    margin: 0 auto 40px;
    width: 160px;
    height: 134px;
  }

  &__title {
    margin: 0 0 4px;
    font-size: 12px;
    line-height: 18px;
    font-weight: 500;
    color: $text-accent-color;
  }

  &__code {
    margin-bottom: 16px;
    color: $text-secondary-color;
  }

  &__review-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 24px;
  }

  .card:hover &__title {
    color: #34568b;
  }

  .stars {
    display: flex;
  }

  .stars__star {
    background: $background-image no-repeat center;
    height: 16px;
    width: 16px;
    margin-right: 4px;

    &:nth-child(-n+4) {
      background-image: $background-image-active;
    }
  }

  &__review-count {
    color: $text-accent-color;
  }

  &__price-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 16px;
  }

  &__price-label {
    font-size: 12px;
    line-height: 18px;
    color: $text-secondary-color;
  }

  &__price {
    font-size: 16px;
    line-height: 18px;
    font-weight: bold;
    color: $text-accent-color;
  }

  &__button {
    margin-bottom: 16px;
    &-buy {
      width: 166px;
      height: 40px;
      background: $main-color;
      border-radius: 5px;
      display: flex;
      justify-content: center;
      text-align: center;
      text-decoration: none;
      font-weight: 700;
      font-size: 14px;
      line-height: 40px;
      text-transform: uppercase;
      color: $white-color;

      &:hover {
        background-color: $white-color;
        border: 1px solid $main-color;
        color: $main-color;
      }
    }
  }
}

@media (max-width: 1024px) {
  .cards {
    padding: 50px 40px;
  }
}
